"use client"
import Link from "next/link"
import Image from "next/image"
import { useAuth, UserButton } from "@clerk/nextjs";
import { Button } from "./ui/Button"
import { ModeToggle } from "./ModeToggle"

export function Navbar() {
  const { isLoaded, userId } = useAuth();

  return (
    <header className="sticky top-0 z-50 w-full border-b border-slate-700/60 bg-slate-900/80 backdrop-blur">
      <div className="container flex h-20 items-center px-6">
        <div className="mr-4 flex items-center">
          <Link href="/" className="mr-6 flex items-center space-x-3">
            <Image src="/sl.png" alt="SoloBot Logo" width={48} height={48} />
            <span className="font-bold text-lg text-white sm:inline-block">
              SoloBotAgency
            </span>
          </Link>
        </div>
        
        <div className="flex flex-1 items-center justify-end space-x-4">
          <ModeToggle />

          {/* This logic now correctly handles the loading state */}
          {isLoaded && userId ? (
            // --- Logged-in State ---
            <>
              <Link href="/dashboard">
                <Button variant="outline">Dashboard</Button>
              </Link>
              <UserButton 
                afterSignOutUrl="/"
                appearance={{
                  elements: {
                    userButtonPopoverCard: "bg-slate-900/80 border border-white/10 shadow-xl backdrop-blur-lg rounded-xl w-52",
                    userButtonPopoverProfileSection: "border-b border-white/10 p-4",
                    userProfileText: "text-white text-base",
                    userButtonPopoverProfileIdentifier: "text-slate-400 text-sm",
                    userButtonPopoverActionButton: "text-slate-200 hover:bg-slate-800/50 p-4 text-base",
                    userButtonPopoverActionButtonIcon: "text-brand h-5 w-5",
                    userButtonPopoverFooter: "hidden" 
                  }
                }}
              />
            </>
          ) : (
            // --- Logged-out State ---
            <>
              <Link href="/sign-in">
                <Button variant="ghost">Log in</Button>
              </Link>
              <Link href="/sign-up">
                <Button>Start Free Trial</Button>
              </Link>
            </>
          )}
        </div>
      </div>
    </header>
  )
}

